.s-container{
    position: relative;
    overflow-x: hidden;
}

.s-container .swiper{
    width: 70%;
    height: 12rem;
    position: unset;
    padding: 3.3rem;
}

.s-container .swiper-slide{
    background: white;
    display: flex;
    border-radius: 10px;
    padding: 1rem;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
}

.s-container .swiper-button-prev{
    left: 6rem;
}

.s-container .swiper-button-next{
    right: 6rem;
}

.s-container .swiper-button-prev, .s-container .swiper-button-next{
    color: black;
}


.left-s{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.name{
    display: flex;
    flex-direction: column;
}

.name>:nth-child(1){
    font-size: 1.4rem;
    font-weight: 600;
}

.name>:nth-child(2){
    font-size: 0.7rem;
}

.left-s>:nth-child(2){
    font-weight: bold;
    font-style: 2rem;
}

.left-s>:nth-child(3){
    font-size: 0.6rem;
    border: 1px solid var(--black);
    padding: 5px 10px;
    width: max-content;
    border-radius: 15px;
}

.img-p{
    transform: rotate(-20deg);
    position: absolute;
    right: 0;
    width: 40%;
    height: 100%;
    bottom: -20%;
}

@media screen and (max-width: 769px){
    .s-container, .swiper{
        width: 90%;
        height: 20rem;
        margin: 0px auto;
    }


    .s-container .swiper-button-prev,
    .s-container .swiper-button-next{
        display: none;
    }
}